import { ConsoleLogger, ToolDefinition } from '@multimodal/mcp-agent';
import { MCPBrowserTool } from './base';

/**
 * InteractionTools - Provides browser interaction tools
 * 
 * Implements UI interaction capabilities like clicking, typing,
 * scrolling, and other element interactions.
 */
export class InteractionTools extends MCPBrowserTool {
  constructor(logger: ConsoleLogger) {
    super(logger);
    this.category = 'browser';
  }

  getTools(): ToolDefinition[] {
    const tools: ToolDefinition[] = [];
    
    // Interaction tools from MCP Browser
    const mcpTools = [
      { name: 'browser_click', description: 'Click on an element' },
      { name: 'browser_press_key', description: 'Press a key or keyboard shortcut' },
      { name: 'browser_hover', description: 'Hover over an element' },
      { name: 'browser_scroll', description: 'Scroll the page' },
      { name: 'browser_form_input_fill', description: 'Fill a form input field' },
      { name: 'browser_select', description: 'Select an option from a dropdown' },
      { name: 'browser_get_clickable_elements', description: 'Get all clickable elements on the page' },
      { name: 'browser_read_links', description: 'Get all links on the page' },
      { name: 'browser_screenshot', description: 'Take a screenshot of the page' },
      { name: 'browser_evaluate', description: 'Evaluate JavaScript on the page' },
    ];
    
    // Add MCP tools
    mcpTools.forEach(tool => {
      const toolDef = this.createMCPToolDefinition(tool.name, tool.description);
      if (toolDef) tools.push(toolDef);
    });
    
    return tools;
  }
}
